<template>
  <div class="container">
    <div class="form-fields" id="form">
      <h2>Choose your knowledge strength</h2>
      <div id="wrapper">
        <select name="select" id="selectbox" v-model="user.region">
        <option v-for="region in game.regions" :key="region">{{region}}</option>
      </select>
      <button @click="get" id="button">Start Quiz</button>
      </div>
    </div>
  </div>
</template>
<script>
import { mapState } from "vuex";
//import { mapActions } from 'vuex'

export default {
  data() {
    return {
      user: {
        region: "",
        page: 1,
      },
    };
  },
  methods: {
    get() {
      if (this.user.region == "All") {
        this.$store.dispatch("getAll", this.user);
      } else {
        this.$store.dispatch("getGameData", this.user);
      }
      this.$store.dispatch("setRegion", this.user);
    },
  },
  computed: mapState({
    game: "game",
  }),

  // mounted(){
  //   this.get()
  // }
};
</script>
