<template>
  <div class="form-fields">
    <img :src="game.answer.flag" alt="flag" :style="flagStyle" />
    <p class="title">This flag belongs to which country?</p>
    
    <div
      v-for="(data, index) in game.data"
      :key="data.id"
      @click.once="resolve(index)"
      v-bind:class="{'active': index == activeIndex, 'correct': (index == game.answerIndex && clicked)}"
    >
      <p>{{data.name}}</p>
    </div>
    <button @click="scorer">Next</button>
  </div>
</template>
<script>
import { mapState } from "vuex";

export default {
  data() {
    return {
      activeIndex: null,
      clicked: false,
      flagStyle: {
        width: "200px",
        height: "100px",
      },
    };
  },
  methods: {
    resolve(index) {
      if (!this.clicked) {
        this.clicked = true;
        this.activeIndex = index;
      }
    },
    scorer() {
      if (this.activeIndex == this.game.answerIndex) {
        this.$store.dispatch("increaseScore");
      }
      this.$store.dispatch('getResults')

    },
  },
  computed: mapState({
    game: "game",
  }),
};
</script>
<style  lang="scss">
.form-fields {
  height: 700px;
}
@media screen and (max-width: 414px){
  .form-fields{
    height: 450px
  }
}


</style>
