<template>
  <div class="form-fields" id="resultbox">
    <img src="../assets/winner.svg" alt="winner-image" />
    <h1>Results</h1>
    <p>
      You got
      <span :class="{'red': game.score < 3, 'green': game.score >= 3}">{{game.score}}</span> correct answer(s)
    </p>
    <span v-if="game.score == 0 ">
      <img src="../assets/shocked.png" alt="" class="icon">
    </span>
    <span v-else-if="game.score < 3">
      <img src="../assets/laugh.png"  class="icon" />
    </span>
    <span v-else>
      <img src="../assets/wink.png" alt="" class="icon">
    </span>
    <button id="replay" @click="replay">Play again</button>
  </div>
</template>

<script>
import { mapState } from "vuex";
export default {
  data() {
    return {};
  },
  methods: {
    replay() {
      this.$store.dispatch("replay");
    },
  },
  computed: mapState({
    game: "game",
  }),
};
</script>

<style lang="scss">
#resultbox {
  text-align: center;
}
#replay {
  float: none;
  background: white;
  color: #2f527b;
  transition:  1s
}
#replay:hover{
  background: #2f527b;
  color: white;
}
#resultbox p {
  font-size: 1.5em;
  margin-bottom: 5px
}
.green {
  color: green;
}
.red {
  color: red;
}
.icon{
  height: 50px;
  display: block;
  text-align: center;
  margin: 0 auto;
  transition: 1s;
}
.icon:hover{
  transform: rotate(360deg);
}
@media screen and (max-width:414px){
  .icon{
    height: 40px
  }
  #resultbox p{
    margin: 5px auto
  }
  #replay{
    margin-top: 15px
  }
}
</style>