import Vue from 'vue'
import VueRouter from 'vue-router'
//import Home from '../views/Home.vue'
import home from '../components/home.vue'
import showQuiz1 from '../components/quiz1.vue'
import showQuiz2 from '../components/quiz2.vue'
import showQuiz3 from '../components/quiz3.vue'
import showQuiz4 from '../components/quiz4.vue'
import showQuiz5 from '../components/quiz5.vue'
import results from '../components/results.vue'
Vue.use(VueRouter)

const routes = [
  {
    path: "/",
    component: home
  },
  {
  path: "/quiz1",
  component: showQuiz1
  },
  {
  path: "/quiz2",
  component: showQuiz2
  },
  {
  path: "/quiz3",
  component: showQuiz3
  },
  {
  path: "/quiz4",
  component: showQuiz4
  },
  {
  path: "/quiz5",
  component: showQuiz5
  },
  {
  path: "/results",
  component: results
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router