<template>
  <div class="form-fields">
    <p class="title">
      Which of the countries is in the {{game.answer.subregion}} subregion and is also in the
      <span v-for="timezone in game.answer.timezones" :key="timezone.id">{{timezone}}</span> timezone ?
    </p>

    <div
      v-for="(data, index) in game.data"
      :key="data.id"
      @click.once="resolve(index)"
      v-bind:class="{'active': index == activeIndex, 'correct': (index == game.answerIndex && clicked)}"
    >
      <p>{{data.name}}</p>
    </div>
    <button @click="scorer">Next</button>
  </div>
</template>
<script>
import { mapState } from "vuex";

export default {
  data() {
    return {
      activeIndex: null,
      clicked: false,
      user: {
        page: 5,
        region: null,
      },
    };
  },
  methods: {
    resolve(index) {
      if (!this.clicked) {
        this.clicked = true;
        this.activeIndex = index;
      }
    },
    scorer() {
      if (this.activeIndex == this.game.answerIndex) {
        this.$store.dispatch("increaseScore");
      }
      if (this.game.region == "All") {
        this.$store.dispatch("clearGameData");
        this.$store.dispatch("getAll", this.user);
      } else {
        this.user.region = this.game.region;
        this.$store.dispatch("clearGameData");
        this.$store.dispatch("getGameData", this.user);
      }
    },
  },
  computed: mapState({
    game: "game",
  }),
};
</script>
<style  lang="scss">
.form-fields {
  height: 600px;
}
</style>
