import Vue from 'vue'
import Vuex from 'vuex'
import router from '../router';
//import router from '../router';
//import router from '../router/index'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    game: {
      data: [],
      state: false,
      region: null,
      regions: ['Europe', 'Africa', 'Americas', 'Asia', 'Oceania', 'All'],
      answer: {},
      answerIndex: null,
      score: 0
    }

  },
  mutations: {
    setGameData(state, val) {
      let num = Math.floor(Math.random() * (4 - 0) + 0);
      state.game.answer = val[num]
      state.game.answerIndex = num
      state.game.data = val
      console.log(state.game.region)
    },
    increaseScore(state) {
      state.game.score += 1;

    },
    setRegion(state, user) {
      state.game.region = user.region;
    },
    resetRegion(state) {
      state.game.region = null;
      state.game.score = 0
    },
    clearGameData(state) {
      state.game.data = [];
      state.game.answer = {};
      state.game.answerIndex = null;
    }

  },
  actions: {
    async getGameData({
      dispatch
    }, user) {
      try {
        let response = await fetch(
          `https://restcountries.eu/rest/v2/region/${user.region}`);
        const data = await response.json();
        dispatch('trimData', data)
        router.push(`/quiz${user.page}`)
      } catch (err) {
        alert('There has been an error, try again :(')
        dispatch("clearGameData");
        router.push("/");
      }
      
    },
    async getAll({
      dispatch
    }, user) {
      try {
        let response = await fetch(`https://restcountries.eu/rest/v2/all`);
        let data = await response.json();
        dispatch('trimData', data)
        router.push(`/quiz${user.page}`)
       
      } catch (err) {
        alert('There has been an error, try again :(')
        dispatch('clearGameData')
        router.push("/")
      }
      
    },
    trimData({
      commit
    }, data) {
      let num = Math.floor(Math.random() * (data.length - 4) + 4)
      let quizdata = data.slice(num - 4, num)
      commit('setGameData', quizdata)

    },
    setRegion({
      commit
    }, val) {
      commit('setRegion', val)
    },
    increaseScore({
      commit
    }) {
      commit('increaseScore')
    },
    clearGameData({
      commit
    }) {
      commit('clearGameData')
    },
    getResults() {
      router.push("/results")
    },
    replay({
      commit
    }) {
      commit('clearGameData');
      commit('resetRegion')
      router.push("/")
    }


  },
  modules: {}
})