<template>
  <div class="form-fields">
    
    <p class="title">{{game.answer.capital}} is the capital of</p>
    
    <div
      v-for="(data, index) in game.data"
      :key="data.id"
      @click.once="resolve(index)"
      v-bind:class="{'active': index == activeIndex, 'correct': (index == game.answerIndex && clicked)}">
      <p>{{data.name}}</p>
      </div>
    <button @click='scorer'>Next</button>
  </div>
</template>
<script>
import { mapState } from "vuex";

export default {
  data() {
    return { 
      activeIndex: null,
      clicked: false,
      user:{ 
        page: 2,
        region: null
      }
    }
  },
  methods: {
    resolve(index){
      if(!this.clicked){
        this.clicked = true;
        this.activeIndex = index;
      } 
    },
    scorer(){
      if(this.activeIndex == this.game.answerIndex){
        this.$store.dispatch('increaseScore')
      }if(this.game.region == 'All'){
        this.$store.dispatch('clearGameData');
        this.$store.dispatch('getAll', this.user)
      }else{
        this.user.region = this.game.region
        this.$store.dispatch('clearGameData');
        this.$store.dispatch('getGameData', this.user)
      }
    }
  },
  computed: mapState({
    game: "game",
  }),
  // mounted(){
  //   console.log(this.game.data)
  // }
};
</script>
<style lang="scss">
.form-fields {
  height: 600px;
  position: absolute;
  top: 200px;
  left: 25%;
}
.title{
  font-size: 1.8em
}
.form-fields div {
  margin: 10px auto;
  border-radius: 12px;
  border: 1px solid;
  padding: 5px 10px;
  cursor: pointer;
  transition: 0.5s
}
.form-fields div p{
  font-size: 1.2em;
}
// .form-fields div:hover{
//   background-color: #F9A826;
//   color: white;
// }
.active{
  background-color: #EA8282;
  color: white
}
.correct{
  background-color: #60BF88;
  color: white
}
button{
  background-color: #F9A826 ;
  border: 1px solid;
  float: right;
  padding: 10px 20px;
  box-sizing: border-box;
  margin-top: 10px;
  text-decoration: none;
  color: white;
  border-radius: 8px;
  line-height: 25px;
  font-size: 1.2em;
  outline: none;
  transition: 1s
}
button:hover{
  background: white;
  color: #F9A826;
}
@media  screen and (max-width: 768px){
  .form-fields{
    left: 20%
  }
}
@media screen and (max-width: 600px){
  button{
    padding: 5px 7px;
  }
  .form-fields{
     padding: 80px 5px 20px 5px;
     width: 90%;
     left: 3.9%
  }
}
@media screen and (max-width: 414px){
  .title{
    font-size: 1.4em;
  }
  .form-fields div p{
    margin: 0;
    font-size: 1em;
  }

  button{
    padding: 5px 15px;
    font-size: 1em
  }
}
@media screen and (max-width: 375px){
  .form-fields{
    left: 3.7%
  }
}
</style>
